import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export class IconPattern extends Component {
    constructor(props) {
        super(props);
        this.icons = this.props.icons.sort(() => 0.5 - Math.random()).slice(0, this.props.num || this.props.icons.length);
        this.posLeft = Array.from(Array(this.props.num || this.props.icons.length)).map(() => Math.random() * (window.innerWidth - this.props.iconSize * 4) + this.props.iconSize * 2)
        this.posTop = Array.from(Array(this.props.num || this.props.icons.length)).map(() => Math.random() * (window.innerHeight - this.props.iconSize * 4) + this.props.iconSize * 2)
    }

    render() {
        return <div style={{ position: "absolute", width: "100%", height: "100%" }}> {
            Object.keys(this.icons).map((key) => {
                return <FontAwesomeIcon
                    key={key}
                    icon={this.icons[key]}
                    style={{
                        position: "absolute",
                        left: this.posLeft[key],
                        top: this.posTop[key],
                        color: this.props.color,
                        fontSize: this.props.iconSize,
                    }} />
            })
        } </div>;
    }
}

IconPattern.defaultProps = {
    color: "white",
    iconSize: 20,
}